@import '../../style/variables.scss';

.randomchar {
    display: grid;
    grid-template-columns: repeat(2, 50%);
    align-items: center;
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.25);
    &__block {
        padding: 40px 35px;
        display: grid;
        grid-template-columns: 180px auto;
        column-gap: 30px;
    }
    &__img {
        width: 180px;
        height: 180px;
        object-fit: cover;
    }
    &__img_not-founded {
        object-fit: contain;
    }
    &__info {
        display: grid;
        grid-template-rows: minmax(29px, auto) 90px 38px;
        row-gap: 10px;
        padding-top: 3px;
    }
    &__name {
        font-weight: bold;
        font-size: 22px;
        line-height: 29px;
        text-transform: uppercase;
    }
    &__descr {
        font-size: 14px;
        line-height: 18px;
    }
    &__btns {
        a:nth-child(1) {
            margin-right: 30px;
        }
    }
    &__static {
        padding: 40px 35px;
        background-color: $dark;
        position: relative;
        button {
            margin-top: 13px;
        }
    }
    &__title {
        font-weight: bold;
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.045em;
        color: #FFFFFF;
        &:nth-child(2) {
            margin-top: 33px;
        }
    }
    &__decoration {
        position: absolute;
        bottom: 14px;
        right: -37px;
    }
}

@media (max-width: 1199px) {
    .randomchar {
        grid-template-columns: 1fr;
    }
}

@media (max-width: 768px) {
    .randomchar {
        &__block {
            padding: 15px 8px;
        }
        &__name {
            font-size: 20px;
        }
        &__static {
            padding: 15px 20px;
        }
        &__title {
            font-size: 20px;
            line-height: 28px;
        }
        &__title:nth-child(2) {
            margin-top: 20px;
        }
        &__decoration {
            transform: scale(0.9);
            bottom: 0px;
            right: -50px;
        }
        &__btns {
            display: flex;
        }
    }
}

@media (max-width: 576px) {
    .randomchar {
        &__block {
            grid-template-columns: 1fr;
            place-items: center;
        }
        &__img {
            width: 200px;
            height: 200px;
        }
        &__btns {
            justify-content: center;
        }
        &__title {
            font-size: 19px;
            line-height: 24px;
        }
        &__decoration {
            transform: scale(0.6);
            bottom: -32px;
            right: -70px;
        }
    }
}